.icon {
    margin: 0 15px;
    position: relative; 
    display: block;
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 63px;
    background: #333;
    border-radius: 50%;
    font-size: 30px;
    color: #666;
    -webkit-transition: .5s;
    transition: .5s;
  }
  
  .icon::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background:  #00adb5;
    -webkit-transition: .5s;
    transition: .5s;
    -webkit-transform: scale(.9);
            transform: scale(.9);
    z-index: -1;
  }
  
  .icon:hover::before {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0 0 15px  #00adb5;
  }
  
  .icon:hover {
    color:  #00adb5;
    box-shadow: 0 0 5px  #00adb5;
